<script>
import tempMovies from "../../assets/mocks/movies-data.json";
export default {
  name: "MoviesSegment",
  data() {
    return {
      currentPage: "home",
      formData: {
        cinema: ""
      },
      iBankLoading: null,
      newCustomerLoading: null,
      allMovies: null,
      selectedTicket: null,
      iMovieLoading: false,
      errors: {},
      showModal: true,
      show: false,
      // show_second: false,
      // show_third: false,
      modalClass: "modal-border-0"
    };
  },
  components: {
    HeaderTab: () => import("../../components/layout/Header"),
    FooterTab: () => import("../../components/layout/Footer"),
    ZButton: () => import("../../components/utils/CustomButton"),
    Loader: () => import("../../components/utils/Loader"),
    LinedTitle: () => import("../../components/utils/LinedTitle"),
    ModalDiv: () => import("../../components/utils/Modal")
    // StackModal: () => import ("@innologica/vue-stackable-modal")
  },
  methods: {
    getMovie() {
      this.iMovieLoading = true;
      setTimeout(() => {
        this.$awn.success("Your free Movie activation was successful", {
          labels: {
            success: "Processing"
          }
        });
        this.iMovieLoading = false;
        window.$("#exampleModalLong").modal("hide");
        return (this.show = false);
      }, 3000);
    },
    validateImage(imageFile) {
      return imageFile
        ? `url(${imageFile})`
        : `url('../../assets/img/place-holder.jpg')`;
    },
    selectTicket(data) {
      this.selectedTicket = null;
      this.selectedTicket = data;
    }
  },
  created() {
    // Prod
    // this.axios.get(`/findmovies`).then(response => {
    //   this.allMovies = response.data;
    //   // eslint-disable-next-line
    //   console.log(response.data);
    // });

    // Preview/Test
    setTimeout(() => {
      this.allMovies = tempMovies;
    }, 2000);
  }
};
</script>

